.main-card{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: .5rem;
    padding: .5rem;
    height: 500px;
    width: 200px;
    border-radius: var(--borderr05);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    overflow: hidden;
    transition: var(--time05);
}
.main-card-filter{
    position: absolute;
    inset: 0;
    background-image: linear-gradient(0deg, var(--bgAvzCardColor));
}
.main-card-title, .main-card a{
    position: relative;
}
.main-card-title{
    font-size: 1.3rem;
    transition: var(--time03) ease var(--time03);
}


@media screen and (min-width: 700px) {
    .main-card{
        flex-grow: 1;
        border-radius: 0;
        height: 100%;
        width: 0;
    }
    .main-card.focus{
        flex-grow: 4;
    }
    .main-card.focus .main-card-title{
        font-size: 1.3rem;
    }
    .main-card-title{
        font-size: .8rem;
        transform: translateY(2.5rem);
    }
    .main-card.focus h3{
        opacity: 1;
        transform: translateY(0);
    }
    .main-card .button{
        opacity: 0;
    }
    .main-card.focus .button{
        opacity: 1;
    }
}