.loaderApp{
    position: fixed;
    inset: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    gap: 2rem;
    text-align: center;
    font-size: 1.3rem;
    background-color: var(--bgColor);
    z-index: 1100;
}
.loaderApp > .loader{
    height: auto;
    width: auto;
}
.loaderApp > .loader > span{
    width: 3rem;
    height: 3rem;
}
.loader{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    grid-column: 1 / -1;
}
.loader > span{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: 2px solid transparent;
    border-top: 2px solid var(--mainColor);
    animation: rotate 1s ease infinite;
}
@keyframes rotate {
    0%{
        transform: rotate(0);
    }
    100%{
        transform: rotate(360deg);
    }
}