.header{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    transition: var(--time05);
    z-index: var(--maxZIndex);
}
.header-content{
    display: grid;
    grid-template-columns: 1fr max-content;
    grid-auto-flow: dense;
    align-items: center;
    gap: .5rem;
    padding: .5rem 1rem 0;
    min-height: var(--headerHeight);
    /* background-image: linear-gradient(180deg, var(--bgHeaderColor)); */
}
.logo{
    font-size: 2rem;
    font-weight: 900;
    text-transform: uppercase;
}
.header-search{
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: 1fr max-content;
    align-items: center;
    background-color: var(--bgCardAlphaColor);
    border-radius: var(--borderr10);
    overflow: hidden;
}
.header-search input{
    padding: .5rem;
    border: none;
    outline: none;
    font-size: 1rem;
    font-family: inherit;
    font-weight: bold;
    color: var(--textColor);
    background-color: inherit;
}
.header-btn{
    position: relative;
    justify-self: end;
}
.header-btn > ion-icon {
    padding: 0 .5rem;
    font-size: 1.5rem;
}
.header-settings{
    position: absolute;
    top: calc(var(--headerHeight) + 1.5rem);
    right: 0;
    width: 280px;
    border-radius: var(--borderr10);
    background-color: var(--bgCardColor);
    opacity: 0;
    visibility: hidden;
    transition: var(--time05);
}
.header-settings.active{
    opacity: 1;
    visibility: visible;
}
.header-setting{
    display: flex;
    align-items: center;
    gap: .5rem;
    padding: 1rem;
    font-size: 1.2rem;
    color: var(--textColor);
}

@media screen and (min-width: 700px) {
    .header{
        background-color: var(--bgAlphaColor);
    }
    .header-content{
        grid-template-columns: max-content 1fr max-content;
        align-items: center;
    }
    .header-search{
        justify-self: center;
        grid-column: span 1;
        width: 100%;
        max-width: 500px;
    }
    .header-content > ion-icon{
        grid-column: 3 / 4;
    }
    .header-settings{
        top: var(--headerHeight);
    }
}