.favorites-content{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: calc(var(--headerHeight) + 2rem);
}
.favorites-cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1rem;
}
.favorites-title{
    font-size: 2rem;
}
.favorites0{
    text-align: center;
    color: var(--textAlphaColor);
}