:root{
  --mainColor: hsl(210, 60%, 50%);
  --bgColor: #222;
  --bgAlphaColor: #2229;
  --bgCardColor: #444;
  --bgCardAlphaColor: #4445;
  --textColor: #fafafa;
  --textAlphaColor: #fafafa89;
  --bgAvzColor: var(--bgColor), transparent, transparent;
  --bgHeaderColor: var(--bgColor) 20%, transparent;
  --bgAvzCardColor: var(--bgCardColor), transparent, transparent;
  --maxWidth: 1000px;
  --headerHeight: 60px;
  --borderr20: 2rem;
  --borderr15: 1.5rem;
  --borderr10: 1rem;
  --borderr05: .5rem;
  --borderr03: .3rem;
  --deskMedia: 600px;
  --maxZIndex: 1000;
  --time10: 1s;
  --time05: .5s;
  --time03: .3s;
  --blur30: blur(30px);
  --whiteAlphaColor: #fffb; 
  --yellowColor: hsl(42, 99%, 60%); 
}
html{
  box-sizing:border-box;
  font-family: sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;
  background-color: var(--bgColor);
  color: var(--textColor);
}
*,*::after,*::before{
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  line-height: 1;
}
body{
  min-height: 100vh;
}
body > *{
  min-height: 100vh;
}



a{
  text-decoration: none;
  color: var(--mainColor);
}
button{
  border: none;
  outline: none;
  font-size: inherit;
  font-family: inherit;
}
ion-icon{
  color: var(--textColor);

}


.button{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: .3rem;
  padding: 0 1rem;
  height: 2.5rem;
  border-radius: var(--borderr05);
  font-weight: bold;
  border: none;
  outline: none;
  font-size: 1rem;
  font-family: inherit;
  background-color: var(--mainColor);
  color: var(--textColor);
  transition: var(--time03);
}
.button ion-icon{
  font-size: 130%;
  color: inherit;
}
.button span{
  text-align: center;
}
.content{
  width: 100%;
  max-width: var(--maxWidth);
  margin: 0 auto;
  padding: 0 1rem;
}
.text{
  color: var(--textAlphaColor);
}
a, button,.cursor{
  cursor: default;
}
@media (hover:hover){
  a,button,.cursor{
    cursor: pointer;
  }
}