.details{
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.details-filter{
    position: absolute;
    inset: 0;
    backdrop-filter: var(--blur30);
}
.video-container{
    position: fixed;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--bgAlphaColor);
    z-index: 1100;
}
.video{
    height: 500px;
}
.details-content{
    position: relative;
    padding: 0rem;
    min-height: 100vh;
    background-color: var(--bgColor);
    overflow: hidden;
    transition: var(--time10);
}
.details-header{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 1rem;
    gap: 1rem;
    min-height: 70vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
}
.details-header >*{
    position: relative;
}
.details-header-filter{
    position: absolute;
    inset: 0;
    background-image: linear-gradient(0deg, var(--bgAvzColor));
}
.details-title{
    font-size: clamp(1.5rem, 6vw, 2.5rem);
}
.details-header-info{
    display: flex;
    gap: 1rem;
    align-items: center;
    font-weight: bold;
    font-size: .8rem;
}
.details-header-info > .date{
    padding: .2rem .5rem;
    border-radius: var(--borderr03);
    background-color: var(--whiteAlphaColor);
    color: var(--bgColor);
}
.details-header-info > .date.new{
    background-color: var(--mainColor);
}
.details-buttons{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: .7rem;
}
.details-main{
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 1rem;
    background-color: var(--bgColor);
}
.details-main-aside{
    display: flex;
    flex-direction: column;
    gap: .3rem;
}
.similar-container, .similar-error{
    grid-column: 1 / -1;
}
.similar-title{
    margin: 2rem 0 .5rem;
}
.similars{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: .5rem;
}
.similar-error{
    text-align: center;
    color: var(--textAlphaColor);
}

@media screen and (min-width: 600px){
    .details-content{
        top: calc(1rem + var(--headerHeight));
        border-radius: var(--borderr15);
        width: 90%;
        min-height: auto;
    }
    .details-buttons{
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));

    }
    .details-main{
    grid-template-columns: 2fr 1fr;
    }
    .similars{
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }
}