.similar{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: .5rem;
    padding: .5rem;
    height: 350px;
    border-radius: var(--borderr10);
    background-color: var(--bgCardColor);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    overflow: hidden;
}
.similar > *{
    position: relative;
}
.similar-filter{
    position: absolute;
    inset: 0;
    background-image: linear-gradient(0deg, var(--bgAvzCardColor));
}
.similar-card-title{
    font-size: 1.1rem;
}
.fav-btn{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .5rem;
    top: .5rem;
    right: .5rem;
    width: 2rem;
    max-width: 15rem;
    height: 2rem;
    border-radius: 50%;
    font-size: .8rem;
    background-color: var(--textColor);
    color: var(--bgColor);
    overflow: hidden;
    transition: var(--time03);
}
.fav-btn.isFav{
    background-color: var(--mainColor);
    color: var(--textColor);
}
.fav-btn > ion-icon{
    color: inherit;
    font-size: 120%;
}
.fav-btn > span{
    display: none;
    transition: var(--time03);
    width: 0;
    font-weight: bold;
    line-height: 1.2;
    overflow: hidden;
}

@media (hover:hover){
    .fav-btn{
        right: 0;
        border-radius: var(--borderr10) 0 0 var(--borderr10);

    }
    .fav-btn:hover{
        width: 90%;
    }
    .fav-btn:hover > span{
        display: inline;
        width: 70%;
    }
}

@media screen and (min-width: 700px){
    .fav-btn{
        top: 1rem;
        right: 0;
        width: 2.5rem;
        border-radius: var(--borderr10) 0 0 var(--borderr10);
        
    }
}