.search-content{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: calc(var(--headerHeight) + 3rem);
    margin-bottom: 1rem;
}
.search-title{
    font-size: clamp(1.5rem, 6vw, 2rem);
}
.search-cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(170px, 1fr));
    gap: .5rem;
}
.search-cards-title{
    position: relative;
    grid-column: 1 / -1;
    margin: 1rem 0 .5rem;
}
.search-cards-title::after{
    content: "";
    position: absolute;
    top: 110%;
    left: .2rem;
    height: 3px;
    width: 10%;
    border-radius: var(--borderr10);
    background-color: var(--mainColor);
}
.search-0{
    grid-column: 1 / -1;
    color: var(--textAlphaColor);
}


@media screen and (min-width: 700px) {
    .search-content{
    margin-top: calc(var(--headerHeight) + 1rem);
    }
    .search-cards{
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        gap: 1rem;
    }
    .search-cards-title{
        margin: 1rem 0 0rem;
    }
    .search-cards-title::after{
    width: 5%;
    }
}