.main-cards{
    display: grid;
    gap: 1rem;
}
.main-cards-container{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: .5rem;
    height: 450px;
    overflow: auto;
}


@media screen and (min-width: 700px){
    .main-cards-container{
        flex-direction: row;
        flex-wrap: nowrap;
        gap: 0;
    }
}